import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import Experience from "./_experience"

import ResumeData from "../assets/json/resume-data.json"

const works = ResumeData.work

const EducationPage = () => (
  <Layout>
    <SEO title="Experiences" />
    <section
      className="resume-section p-3 p-lg-5 d-flex justify-content-center animated fadeIn"
      id="experience"
    >
      <div className="w-100">
        <h2 className="mb-5">Experiences</h2>
        {works.map((work, index) => {
          return <Experience data={work} key={index} />
        })}
      </div>
    </section>
  </Layout>
)

export default EducationPage
