import React, { Component } from "react"
import Moment from "react-moment"

interface IExperience {
  data: {
    company: string
    position: string
    website: string
    startDate: string
    highlights: string[]
    endDate?: string
  }
}

export class Experience extends Component<IExperience> {
  constructor(props: IExperience) {
    super(props)
  }
  render() {
    const work = this.props.data
    const workStart = <Moment format="MMMM YYYY">{work.startDate}</Moment>
    const workEnd = work.endDate ? (
      <Moment format="MMMM YYYY">{work.endDate}</Moment>
    ) : (
      "Present"
    )
    return (
      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
        <div className="resume-content">
          <h3 className="mb-0">{work.position}</h3>
          <div className="subheading mb-3">{work.company}</div>
          <ul>
            {work.highlights.map((highlight, index) => {
              return <li key={index}>{highlight}</li>
            })}
          </ul>
        </div>
        <div className="resume-date text-md-right">
          <span className="text-primary">
            {workStart} - {workEnd}
          </span>
        </div>
      </div>
    )
  }
}

export default Experience
